@import "../../common/styles.scss";

.movie-list {
    margin: 20px 0;
    h1 {
        color: $secondary-color;
        margin-bottom: 10px;
        font-weight: 400;
        text-align: center;
    }
    
    h2 {
        color: $secondary-color;
        margin-bottom: 10px;
        font-weight: 400;
    }
}

.movie-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    grid-gap: 15px;
}